import React, { Component } from "react";
import Dropdown from 'react-dropdown';
import Loader from 'react-loader-spinner'
import { CSVLink } from "react-csv";
import CSVReader from "react-csv-reader";
import * as math from 'mathjs'

import SettingsIcon from '../../img/settings.png';
import SettingsIconHighlighted from '../../img/settings_highlighted.png';


let current_service = null;
let currentDevice  = null;
let packet_count_start = -2;
let packet_count = packet_count_start;
let meta_data = [];
let dataBuffer = [];
let output_data = [];
let curve_count = 0;
let new_message = "";
let current_curve_data = {};
let all_curve_data = [];
let automation_configs = []
let automated_test_count = 0;


class SettingsMenu extends Component {

  constructor(props) {
    console.log("Build Version 0.090");
    super(props);
    this.state = {
      preparing_save: false,
      save_ready: false,
      status_message: "",
      user_disconnected: false,
      loading_file: false,
      automated_test: false,
      characteristics_menu_open: false,
      loading: false,
      device_name: null,
      device_paired: null,
      current_service: null,
      menu_open: false,
      running_test: false,
      cart_detect: 0,
      frequency_NR: 180,
      frequency: 400,
      start_voltage: -300,
      range: 7,
      range_NR: 7,
      we_selected: 1,
    }
  }

  exportedData = () => {
    console.log("Data Exported: ", dataBuffer);
  }

  /* -- No longer used
  calculate_data_value = (raw_value) => {
    if (this.state.range == 7) {
        return (0.0333 * raw_value) - 502.12;
    } else if (this.state.range == 5) {
        return (0.0012 * raw_value) - 49.787;
    } else if (this.state.range == 4) {
        return (0.0001 * raw_value) - 3.309;
    } else if (this.state.range == 3) {
        return (0.00001 * raw_value) - 0.2719;
    }
  }
  */

  handleCharacteristicValueChanged = (event) => {
    let dv = event.target.value;
    //console.log(packet_count, dv, dv.getInt32(0, true), dv.getInt32(4, true), dv.getInt32(8, true), dv.getInt32(12, true));
    //console.log("Raw data packet ( packet: " + packet_count + " ): [ "+ dv.getInt32(0, true) + ", "+ dv.getInt32(4, true) + ", "+ dv.getInt32(8, true) + ", "+ dv.getInt32(12, true) + ", "+ dv.getInt32(16, true) + "]");
    if (packet_count < 0) { //The initial 2 packets contain meta data about the test
      this.setState({loading: true});
      if (packet_count == -1) {
        current_curve_data["Range"] = dv.getInt32(0, true);
        current_curve_data["Peak_Current"] = dv.getUint32(4, true);
        current_curve_data["Error_Code"] = dv.getInt32(8, true);
        if (this.state.automated_test) {
          current_curve_data["Automated_Row_Number"] = automation_configs[0].Order;
        }
        new_message = new_message + " \n \
        Range : "+ dv.getInt32(0, true) + " \n \
        Peak current : "+ dv.getUint32(4, true)  + " \n \
        Error code : "+ dv.getInt32(8, true);
        this.setState({status_message: new_message})
      } else {
        current_curve_data["Curve"] = "y"+curve_count;
        current_curve_data["Device"] = this.state.device_name;
        current_curve_data["Electrode"] = dv.getInt32(8, true);
        current_curve_data["Frequency"] = dv.getInt32(12, true);
        current_curve_data["Start_Voltage"] = dv.getInt32(16, true);
        new_message = this.state.status_message + "\n y"+curve_count+": \n \
        Device : " + this.state.device_name + " \n \
        Electrode : " + dv.getInt32(8, true) + " \n \
        Frequency : " + dv.getInt32(12, true) + " \n \
        Start voltage is: " + dv.getInt32(16, true) 
      }
    } else if (dv.getInt32(0, true) == 0 && dv.getInt32(4, true) == 0 && dv.getInt32(8, true) == 0 && dv.getInt32(12, true) == 0 &&  dv.getInt32(16, true) == 0 && packet_count >= 100 ) {
      let curves = []
      for (let i = 0; i <= curve_count;i++) {
        curves.push(i) 
      }
      meta_data.push(current_curve_data);
      if (curve_count <= 20) { //Limit amount of rows renderable as the app struggles and crashes with too many
        this.props.updateGraphMethod(dataBuffer, curves);
      }
      current_curve_data = {};
      curve_count ++;
      packet_count = packet_count_start;
      this.setState({loading: false});
      if (this.state.automated_test) {
        console.log("Automated row order number: " + automation_configs[0].Order + ", Test " + automated_test_count );
        if (automated_test_count == 1) {
          automation_configs.shift();
          this.run_automated_tests(automation_configs);
        } else {
          automated_test_count --;
        }
      }
      return;
    } else if (packet_count == 99){ 
      //For last packet we ignore the 5th integer which represend end of transmission value
      let x_val = parseInt(this.state.start_voltage) + (packet_count * 5);
      let byte_counter = 0;
      if (curve_count != 0) {
        for (let value of dataBuffer){
          if (byte_counter == 4) {
            break;
          }
          if (value.x == x_val + byte_counter) { //Update Y val in data for X for current curve
            value["y"+curve_count] = dv.getInt32(4 * byte_counter, true);
            byte_counter += 1;
          }
        };
      } else {
        dataBuffer.push(
            {x: x_val,   [ "y" + curve_count ]: dv.getInt32(0, true)},
            {x: x_val+1, [ "y" + curve_count ]: dv.getInt32(4, true)},
            {x: x_val+2, [ "y" + curve_count ]: dv.getInt32(8, true)},
            {x: x_val+3, [ "y" + curve_count ]: dv.getInt32(12, true)},
        )
      }

    } else {

      let x_val = parseInt(this.state.start_voltage) + (packet_count * 5);
      let byte_counter = 0;
      if (dataBuffer.length > 399) {
        for (let value of dataBuffer){
          if (byte_counter == 5) {
            break;
          }
          if (value.x == x_val + byte_counter) { //Update Y val in data for X for current curve
            value["y"+curve_count] = dv.getInt32(4 * byte_counter, true);
            byte_counter += 1;
          }
        };
      } else {
        dataBuffer.push(
            {x: x_val,   [ "y" + curve_count ]: dv.getInt32(0, true)},
            {x: x_val+1, [ "y" + curve_count ]: dv.getInt32(4, true)},
            {x: x_val+2, [ "y" + curve_count ]: dv.getInt32(8, true)},
            {x: x_val+3, [ "y" + curve_count ]: dv.getInt32(12, true)},
        )
      }
    }
    packet_count += 1;
  }

  pairDevice = () => {
    console.log("Trying to pair...");
    let pairedDevice  = null;
    let current_service  = null;
    //Below is a request that uses a filter, so no irrelevant bluetooth devices are shown
    //navigator.bluetooth.requestDevice({filters: [{name: 'Potentiostat'}], optionalServices: ['16d30bc1-f148-49bd-b127-8042df63ded0']})
    navigator.bluetooth.requestDevice({acceptAllDevices: true,optionalServices: ['16d30bc1-f148-49bd-b127-8042df63ded0']})
    .then(device => {
      // Human-readable name of the device.
      console.log("connected to: " + device.name);
      pairedDevice = device;
      pairedDevice.addEventListener('gattserverdisconnected', this.onDisconnected);
      // Attempts to connect to remote GATT Server.
      return device.gatt.connect();
    })
    .then(server => {
      return server.getPrimaryService('16d30bc1-f148-49bd-b127-8042df63ded0');
    })
    .then(service => {
      this.setState({device_name: pairedDevice.name, device_paired: pairedDevice, current_service: service});
    })
    .catch(error => { console.log(error); });
  }

  disconnectDevice = () => {
    if (this.state.device_paired) {
      this.state.device_paired.gatt.disconnect(); 
      this.setState({user_disconnected: true, device_name: null, device_paired: null, current_service: null});
    } 
  }

  onDisconnected = () => {
    console.log('> Bluetooth Device disconnected');
    if(!this.state.user_disconnected) {
      this.reconnectDevice();
    }
  }

  logTime = (text) => {
    console.log('[' + new Date().toJSON().substr(11, 8) + '] ' + text);
  }

  re_run_automated_tests = () => {
    if (this.state.automated_test) {
      let i;
      for(i = (meta_data.length - 1);i >= 0; i--) {
        if (meta_data[i]["Automated_Row_Number"] == automation_configs[0].Order) {
          curve_count --; 
        } else {
          break; 
        }
      }
      console.log("DONE", curve_count);
      this.run_automated_tests(automation_configs);
      return;
    }
  }

  reconnectDevice = () => {
    this.exponentialBackoff(15 /* max retries */, 2 /* seconds delay */,
      () => { //toTry()
        this.logTime('Reconnecting to Bluetooth Device... ');
        return this.state.device_paired.gatt.connect();
      },
      (server) => { //success()
        server.getPrimaryService('16d30bc1-f148-49bd-b127-8042df63ded0').then(service => {
          this.setState({current_service: service});
          console.log('> Bluetooth Device connected. Reconnected');
          this.re_run_automated_tests();
        })
      },
      () => { //fail()
        this.logTime('Failed to reconnect.');
      });
  }

  // This function keeps calling "toTry" until promise resolves or has
  // retried "max" number of times. First retry has a delay of "delay" seconds.
  // "success" is called upon success.
  exponentialBackoff = (max, delay, toTry, success, fail) => {
    toTry().then(result => success(result))
    .catch(e => {
      if (max === 0) {
        return fail();
      }
      console.error(e);
      this.logTime('Retrying in ' + delay + 's... (' + max + ' tries left)');
      setTimeout(() => {
        this.exponentialBackoff(--max, delay * 2, toTry, success, fail);
      }, delay * 1000);
    });
  }


  updateCharacteristics = (run_test_after) => {
    
      this.setState({menu_open: false, loading: true});
      // Dataview needs to be made to make a big endian ArrayBuffer
      let buffer = null;
      let dv = null;

      // Write to BLE_UUID_ZIO_FREQ1_CHAR 
      buffer = new ArrayBuffer(2);
      dv = new DataView(buffer);
      dv.setUint16(0, parseInt(this.state.frequency), false);
      this.state.current_service.getCharacteristic('16d30bc2-f148-49bd-b127-8042df63ded0').then(characteristic => {
       characteristic.writeValue(dv.buffer);
      }).then(_ => {
        // Write to BLE_UUID_ZIO_FREQNR_CHAR 
        buffer = new ArrayBuffer(2);
        dv = new DataView(buffer);
        dv.setUint16(0, parseInt(this.state.frequency_NR), false);
        this.state.current_service.getCharacteristic('16d30bc3-f148-49bd-b127-8042df63ded0').then(characteristic => {
         characteristic.writeValue(dv.buffer);
        }).then(_ => {
          // Write to BLE_UUID_ZIO_START_VOLT_CHAR 
          buffer = new ArrayBuffer(2);
          dv = new DataView(buffer);
          dv.setInt16(0, parseInt(this.state.start_voltage), false);
          this.state.current_service.getCharacteristic('16d30bc4-f148-49bd-b127-8042df63ded0').then(characteristic => {
           characteristic.writeValue(dv.buffer);
          }).then(_ => {
            // Write to BLE_UUID_ZIO_RANGE1_CHAR 
            buffer = new ArrayBuffer(1);
            dv = new DataView(buffer);
            dv.setUint8(0, parseInt(this.state.range), false);
            this.state.current_service.getCharacteristic('16d30bc6-f148-49bd-b127-8042df63ded0').then(characteristic => {
             characteristic.writeValue(dv.buffer);
            }).then(_ => {
              // Write to BLE_UUID_ZIO_RANGENR_CHAR 
              buffer = new ArrayBuffer(1);
              dv = new DataView(buffer);
              dv.setUint8(0, parseInt(this.state.range_NR), false);
              this.state.current_service.getCharacteristic('16d30bc7-f148-49bd-b127-8042df63ded0').then(characteristic => {
               characteristic.writeValue(dv.buffer);
              }).then(_ => {
                // Write to BLE_UUID_WE_SELECT
                buffer = new ArrayBuffer(4);
                dv = new DataView(buffer);
                dv.setUint32(0, parseInt(this.state.we_selected), false);
                this.state.current_service.getCharacteristic('16d30bcb-f148-49bd-b127-8042df63ded0').then(characteristic => {
                 characteristic.writeValue(dv.buffer);
                }).then(_ => {
                  if (run_test_after)
                    this.runTest();
                })
              })
          })
        })
      })

      })

      let message = "\n Characteristics updated: \n \
      Frequency1: "+ this.state.frequency + "\n \
      FrequencyNR: "+ this.state.frequency_NR + "\n \
      Start Voltage: "+ this.state.start_voltage + "\n \
      Range1: "+ this.state.range + "\n \
      RangeNR: "+ this.state.range_NR + "\n \
      WE Selected: "+ this.state.we_selected
      this.setState({status_message: this.state.status_message + message, loading: false});
  }

  runTest = () => {
    console.log("Attempting to run tests...");
    packet_count = packet_count_start;
    //Listen to Data characteristic
    this.state.current_service.getCharacteristic('16d30bc9-f148-49bd-b127-8042df63ded0').then(characteristic => {
      characteristic.startNotifications();
      return characteristic;
    }).then(characteristic => {
      characteristic.addEventListener('characteristicvaluechanged', this.handleCharacteristicValueChanged);
    }).then(_ => {
      //Write 1 to CMD characteristic to start test
      this.state.current_service.getCharacteristic('16d30bc8-f148-49bd-b127-8042df63ded0').then(CMDCharacteristic => {
          console.log("running Tests");
          CMDCharacteristic.writeValue(Uint8Array.of(1));
      })
      .catch(error => { console.log(error); });
      this.setState({loading: true})
    })
    .catch(error => { console.log(error); });

  }

  toggleMenuOpen = () => {
    this.setState({
      menu_open: !this.state.menu_open,
      characteristics_menu_open: false,
      loading_file: false,
      save_ready: false
    })
  }

  open_characteristics_menu = () => {
    this.setState({characteristics_menu_open: true}) 
  }
  
  handleInputParameterChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    });
  }

  open_loading_automated_test = () => {
    this.setState({loading_file: true, automated_test: true});
  }


  open_loading_file = () => {
    this.setState({loading_file: true});
  }

  prepare_save = () => {
    //Calculate Average, SD and RSD for each Frequency/Electrode pair
    this.setState({preparing_save: true});
    let freq_peaks = {}; // {freq_value: [p0, ... pn]}
    let avg_sd_rsd = []; 
    meta_data.forEach((row) => {
      if (row.Peak_Current != 0) {
        if (row.Frequency in freq_peaks) {
          freq_peaks[row.Frequency].push(row.Peak_Current);
        } else {
          freq_peaks[row.Frequency] = [ row.Peak_Current ];
        }
      }
    });
    let freq_rows = Object.keys(freq_peaks);
    freq_rows.forEach((freq) => {
      let avg = math.mean(freq_peaks[freq]) 
      let sd = math.std(freq_peaks[freq]) 
      avg_sd_rsd.push({
        'Frequency_All': freq,
        Average: avg,
        SD: sd,
        RSD: sd/avg,
      })
    })
    output_data = meta_data.concat(dataBuffer);
    output_data = avg_sd_rsd.concat(output_data);
    this.setState({save_ready: true, preparing_save: false});
  }

  handle_load_file = data => {
    this.setState({menu_open: false, loading: true, loading_file: false});
    let headers = data[0];
    let tempData = [];
    for(let i = 1; i < data.length;i++) {
      let tempRow = {}
      headers.forEach((val,index) => {
        tempRow[val] = data[i][index];
      });
      tempData.push(tempRow);
    }
    curve_count = headers.length - 1;
    let curves = [];
    for (let i = 0; i <= curve_count;i++) {
      curves.push(i); 
    }

    if (this.state.automated_test) {
      tempData.sort(function (a, b) {
        return a.Order - b.Order;
      });
      curve_count = 0;
      automation_configs = tempData;
      this.run_automated_tests(tempData);
    } else {
      this.props.updateGraphMethod(tempData, curves);
      this.setState({loading: false, dataBuffer: tempData});
    }
  }

  run_automated_tests = test_configs => {
    if (test_configs.length == 0) {
      this.setState({automated_test: false});
      console.log("FINISHED");
    } else {
      let row = test_configs[0];
      if ( typeof row.Freq === "undefined" || typeof row.FreqNR === "undefined" || typeof row["Start Volt"] === "undefined" || typeof row.Range === "undefined" || typeof row.RangeNR === "undefined" || typeof row["WE Select"] === "undefined" || row.Freq == "" || row.FreqNR == "" || row["Start Volt"] == "" || row.Range == "" || row.RangeNR == "" || row["WE Select"] == "") {
        automation_configs.shift();
        this.run_automated_tests(automation_configs);
      } else {
        console.log(row);
        automated_test_count = (row["WE Select"].split("1").length - 1) * 2;
        this.setState({
          frequency: row.Freq, 
          frequency_NR: row.FreqNR, 
          start_voltage: row["Start Volt"], 
          range: row.Range, 
          range_NR: row.RangeNR, 
          we_selected: row["WE Select"]
        });
        this.updateCharacteristics(true);
      }
    }
    
  }




  render() {
    return (
      <div style={ {zIndex: 1, position: 'absolute', width: '20vw', height: '100vh', right: 0} }>
        {this.state.menu_open ? 
        <div>
            <img  onClick={this.toggleMenuOpen} src={require('../../img/close.png')} alt="Settings" style={ {zIndex: 2, top: 9, right: '6vw', width: 40, cursor: 'pointer', position: 'absolute'} }/>
            {this.state.characteristics_menu_open 
            ? 
              <div className="settings-popup">

                <label className="option">
                  Frequency
                  <div>
                    <input onChange={e => this.handleInputParameterChange(e)} value={this.state.frequency} name="frequency" type="number" className="input-text small"/>
                    Hz
                  </div>
                </label>

                <label className="option">
                  FrequencyNR
                  <div>
                    <input onChange={e => this.handleInputParameterChange(e)} value={this.state.frequency_NR} name="frequency_NR" type="number" className="input-text small"/>
                    Hz
                  </div>
                </label>


                <label className="option">
                  Start Voltage
                  <div>
                    <input onChange={e => this.handleInputParameterChange(e)}  value={this.state.start_voltage}  name="start_voltage" type="number" className="input-text small"/>
                    mV
                  </div>
                </label>


                <label className="option">
                  Range
                  <div>
                    <input onChange={e => this.handleInputParameterChange(e)}  value={this.state.range} name="range" type="number" className="input-text small"/>
                  </div>
                </label>


                <label className="option">
                  RangeNR
                  <div>
                    <input onChange={e => this.handleInputParameterChange(e)}  value={this.state.range_NR} name="range_NR" type="number" className="input-text small"/>
                  </div>
                </label>

                <label className="option">
                  WE Selected
                  <div>
                    <input onChange={e => this.handleInputParameterChange(e)}  value={this.state.we_selected} name="we_selected" type="number" className="input-text small"/>
                  </div>
                </label>



                <div className="flex">
                  <div className="button start-test" onClick={() => {this.updateCharacteristics(false)}}> Send to device</div>
                </div>
              </div>
           :
             <div className="settings-popup">
             {this.state.loading_file 
             ?
              <CSVReader cssClass="load-csv" label="Select csv file to load" onFileLoaded={this.handle_load_file}>
              </CSVReader>
             :
             <div>
                <div className="flex">
                  <div className="button start-test" onClick={this.pairDevice}> 
                    Pair Device
                  </div>
                </div>

                <div className="flex">
                  <div className="button start-test" onClick={this.disconnectDevice}> 
                    Disconnect Device
                  </div>
                </div>

               <div className="flex">

                  {this.state.save_ready ? 
                      <CSVLink onClick={this.exportedData} data={output_data} style={{textDecoration: 'none'}} filename={ this.state.device_name + "_" + (new Date()).toString()} className="button start-test"> 
                        Download Save Data
                      </CSVLink>
                    :
                      <div>
                        {this.state.preparing_save ? 
                          <div style={{padding: "15 0"}}>
                            Preparing...
                          </div>
                        :
                          <div className="button start-test" onClick={this.prepare_save}> 
                            Prepare Save Data
                          </div>
                        }
                      </div>
                  }
                </div>
                <div className="flex">
                  <div className="button start-test" onClick={this.open_loading_file}> 
                    Load Data
                  </div>
                </div>

                <div className="flex">
                  <div className="button start-test" onClick={this.open_loading_automated_test}> 
                    Load Automated Test
                  </div>
                </div>

                <div className="flex">
                  <div className="button start-test" onClick={this.open_characteristics_menu}> 
                    Update Characteristics 
                  </div>
                </div>
             </div>
             }
             </div>
           }
        </div>
        :
        //End is menu open? 
        <div >
          {
            this.state.device_paired == null 
            ? 
            <div className="flex" style={ {position: 'absolute', height: '100vh', right: 50} }>
              <div className="pair-device-button" onClick={this.pairDevice} >
                Pair Device
              </div>
              {dataBuffer.length > 0 ?
                <div className="flex">
                    <CSVLink onClick={this.exportedData} data={dataBuffer} style={{textDecoration: 'none', marginTop: 100}} filename={ this.state.device_name + "_" + (new Date()).toString()} className="button start-test"> 
                      Save Data 
                    </CSVLink>
                </div>
              :
                ""  
              }
            </div>
            :
              <div>
              {
                this.state.loading
              ?
                <div>
                  <br></br><br></br>
                  {dataBuffer.length > 0 ?
                    <div className="flex">
                        <CSVLink onClick={this.exportedData} data={dataBuffer} style={{textDecoration: 'none', marginBottom: 10}} filename={ this.state.device_name + "_" + (new Date()).toString()} className="button start-test"> 
                          Save Data 
                        </CSVLink>
                    </div>
                  :
                    ""  
                  }
                  <Loader type="TailSpin" color="#7dbcd2" height={80} width={80} />
                  <div className="status-message">
                    {this.state.status_message}
                  </div>
                </div>
              :
               <div>
                <img  onClick={this.runTest} src={require('../../img/play_button.png')} alt="Settings"
                  onMouseOver={e => (e.currentTarget.src = require('../../img/play_button_highlighted.png'))}
                  onMouseOut={e => (e.currentTarget.src = require('../../img/play_button.png'))}
                style={ {cursor: 'pointer',top: 10, right: 'calc(6vw + 80px)', position: 'absolute'} }/>

                <img  onClick={this.toggleMenuOpen} src={require('../../img/settings.png')} alt="Settings" 
                  onMouseOver={e => (e.currentTarget.src = require('../../img/settings_highlighted.png'))}
                  onMouseOut={e => (e.currentTarget.src = require('../../img/settings.png'))}
                  style={ {cursor: 'pointer', top: 10, right: '6vw', position: 'absolute'} }/>

                  <div className="device-message">
                    Connected to: {this.state.device_name}
                  </div>

                  <div className="status-message">
                    {this.state.status_message}
                  </div>
               </div>

              }
             </div>
            }
        </div>
        }

      </div>
    )
  }
}

export default SettingsMenu;
